<template>
    <el-row>
        <div v-for="item in nodeDragData" :id="item.id" :key="item.id" class="imgsvgItem" draggable="true" @dragstart="selNode(item.type)" @dragend="nodeDragEnd">
            <svg width="76" height="76">
                <use :xlink:href="'#'+item.type"></use>
            </svg>
        </div>
    </el-row>
</template>
<script>
import { mapMutations, mapState } from "vuex";
export default {
    name: "flow-left-tool",
    data() {
        return {
            nodeDragData: [
                { id: "flowleft1", type: "StartStopNode", name: "起止节点" },
                { id: "flowleft2", type: "RoutineNode", name: "常规节点" },
                { id: "flowleft3", type: "ForkNode", name: "分叉节点" },
                { id: "flowleft4", type: "ModelNode", name: "模型节点" }
            ]
        };
    },
    computed: {
        ...mapState("flow", ["selNodeType"])
    },
    methods: {
        ...mapMutations("flow", ["SEL_NODETYPE"]),
        selNode(type) {
            this.SEL_NODETYPE(type);
        },
        nodeDragEnd() {
            if (this.selNodeType) {
                this.SEL_NODETYPE("");
            }
        }
    }
};
</script>
<style scoped>
.imgsvgItem {
    width: 76px;
    height: 76px;
    border: 1px solid #fff;
    margin-left: auto;
    margin-right: auto;
}
.imgsvgItem:hover {
    border: 1px solid #ccc;
    cursor: move;
}
</style>



